form {
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

body {
  color: #333;
  background-color: #f4ede7;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  font-family: Jost, Nunito Sans, Open Sans, Montserrat, Roboto, sans-serif;
  display: flex;
}

.wrapper {
  --margin: 1rem;
  width: 85%;
  background-color: #fff;
  margin-block: 1rem;
  margin-inline: auto;
  font-size: 1rem;
  box-shadow: .7rem .5rem 1rem .2rem #4d4e4e80;
}

header {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#logo {
  height: 5vh;
  align-items: center;
  padding: 1rem;
  display: block;
}

main h1 {
  color: green;
  text-align: center;
  text-transform: capitalize;
  margin: 0;
  font-family: Crafty Girls, cursive;
  font-size: 1.2rem;
  font-weight: bold;
}

label {
  color: green;
  font-family: Crafty Girls, cursive;
  font-weight: bold;
}

#color-button {
  display: none;
}

ul {
  color: #000;
  text-transform: uppercase;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding-inline: 0;
  font-weight: bold;
  text-decoration: none;
  list-style-type: none;
  display: flex;
}

li a {
  color: #7e7e7e;
  font-weight: 200;
  text-decoration: none;
}

li a:hover {
  color: green;
  background-color: #eef3f3;
  border: 1px solid #0000;
  border-radius: 1rem;
  margin: 5px 0 5px 4px;
  padding: 4px 10px;
  font-weight: bold;
}

main {
  flex-flow: column wrap;
  align-content: space-around;
  gap: 1rem;
  padding-block-end: 5rem;
  display: flex;
}

.turned-leaf {
  transform: rotateY(180deg);
}

.product-container, .product-container-accesorii {
  width: 90%;
  height: 30%;
  flex-direction: column;
  align-content: space-evenly;
  gap: 1rem;
  margin-block: 0;
  margin-inline: auto;
  padding-block: 0;
  display: flex;
}

#facebook, #instagram {
  height: 2rem;
}

form {
  background-color: #f4ede7;
  border: 1px solid green;
  border-radius: 2rem;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2rem;
  display: flex;
}

form:hover {
  box-shadow: .5rem .5rem .5rem .2rem #d4d6d680;
}

#val1 {
  width: 1rem;
}

.form-group input {
  min-width: 20vw;
}

.checkbox-group {
  width: 100%;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  display: flex;
}

textarea {
  width: 20vw;
  min-height: 10vh;
  max-height: 40vh;
  resize: vertical;
}

.btn-group {
  align-self: center;
}

footer {
  color: green;
  background-color: #eef3f3;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding-inline: 1rem;
  display: flex;
}

#footer-contact {
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
  margin: 0;
  display: flex;
}

@media (min-width: 400px) {
  header {
    flex-direction: row;
  }
}

@media (min-width: 700px) {
  .topbar-text {
    background-color: #ff4500;
    display: block;
  }

  header {
    width: 100%;
    padding: 0;
  }

  nav {
    width: 100%;
    padding-inline: 1rem;
  }

  .logo {
    padding-inline: 1rem;
  }

  ul {
    width: 100%;
    flex-direction: row;
    place-content: flex-end;
    align-items: center;
    display: flex;
  }
}

@media (min-width: 500px) {
  #footer-contact {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    gap: 1rem;
    margin: 0;
    display: flex;
  }

  footer {
    color: green;
    background-color: #eef3f3;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding-block-start: 1rem;
    padding-inline: 1rem;
    display: flex;
  }
}

@media (min-width: 920px) {
  li a {
    font-size: 1.3rem;
  }

  ul {
    gap: 1rem;
  }

  #logo {
    height: 7vh;
    padding: 1rem;
  }
}

@media (min-width: 1000px) {
  .wrapper {
    width: 65%;
  }
}

